h2 {
  margin: 0 auto 2rem;
}

body {
  grid-gap: 1rem;
  background-color: #1b69f9;
  grid-template-rows: auto auto auto auto;
  grid-template-columns: 1fr 2fr;
  margin: 2rem;
  font-family: Rubik, sans-serif;
  font-size: 18px;
  display: grid;
}

.header__header {
  color: #fff;
  grid-column: span 3;
  align-items: center;
  margin: 2rem auto 3rem 0;
  display: flex;
}

.header__header > h1 {
  margin-right: 2rem;
  font-size: 3em;
}

.header__city-name {
  font-size: 2rem;
  font-style: oblique;
}

.header__city-name:before, .header__city-name:after {
  content: "✨";
}

.temperature__section, .sky__section, .city-name__section {
  background-color: #fff;
  border-radius: 8px;
  padding: 2rem;
}

.temperature__section {
  grid-row: 2;
}

.temperature__section button {
  color: #fff;
  text-align: center;
  background-color: #1b69f9;
  border: none;
  border-radius: 10px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.sky__section {
  grid-row: 3;
}

.city-name__section {
  grid-row: 4;
}

.garden__section {
  text-align: center;
  grid-area: 2 / 2 / span 3;
  align-self: center;
}

.temperature__content {
  flex-direction: row;
  justify-content: space-around;
  display: flex;
}

#tempValue {
  margin-left: 1.5rem;
  font-size: 3rem;
}

.temperature__controls {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.garden__section > h2 {
  color: #fff;
}

.garden__content {
  border-radius: 8px;
  flex-direction: column;
  justify-content: space-between;
  max-width: fit-content;
  min-height: 200px;
  margin: auto;
  padding: 2rem;
  font-size: 2em;
  display: flex;
}

.city-name__reset-btn {
  color: #fff;
  background-color: #1655cc;
  border: 0;
  border-radius: 8px;
  padding: 1rem;
  font-family: Rubik, sans-serif;
}

